	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/basecamp-for-iphone-made-with-rubymotion/index.md absPath of file >>> MarkdownRemark","html":"<p>Nick Quaranto at 37 Signals describes writing the Basecamp app with RubyMotion so he could avoid using Xcode and learning Objective-C.</p>\n<p>When I started developing iOS apps, I looked at using <a href=\"http://xamarin.com/monotouch\">MonoTouch</a> as I had been writing C# for many years. But I decided the pitfalls of using a third-party layer on top of Objective-C and Cocoa just wasn’t worth it. Objective-C isn’t a very hard language to learn, and I can’t help but think that these kind of tools just encourage developer laziness.</p>","frontmatter":{"title":"Basecamp for iPhone made with RubyMotion","date":"February 14, 2013","passthroughUrl":"http://37signals.com/svn/posts/3432-why-i-loved-building-basecamp-for-iphone-in-rubymotion"}}},"pathContext":{"slug":"/blog/2013/2/basecamp-for-iphone-made-with-rubymotion/","previous":{"fields":{"slug":"/blog/2013/2/a-better-app-store/"},"frontmatter":{"title":"A better App Store","passthroughUrl":"http://www.macworld.com/article/2027485/three-wishes-for-a-better-app-store.html"}},"next":{"fields":{"slug":"/blog/2013/2/being-an-android-user-is-a-lot-like-being-a-mac-user-in-the-90s/"},"frontmatter":{"title":"Being an Android user is a lot like being a Mac user in the 90's","passthroughUrl":"http://37signals.com/svn/posts/3437-being-an-android-user-is-a-lot-like-being-a-mac-user-in-the-90s"}}}}

